import { FC, ChangeEvent, useState, useCallback  } from 'react';
import PropTypes from 'prop-types';
import {
  Tooltip,
  Divider,
  Box,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';


import { read, utils, writeFileXLSX } from 'xlsx';
import { IBrand } from 'src/models/product';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import BulkActions from './BulkActions';
import {FaCloudDownloadAlt} from 'react-icons/fa'
import { api } from 'src/api/api';
import { BrandsModal } from 'src/components/Modals/BrandsModal/BrandsModal';

interface BrandsTableProps {
  className: string;
  brands: IBrand[];
  panel: Boolean;
}

const applyFilters = (
  brands: IBrand[],
): IBrand[] => {
  return brands.filter((brands) => {
    let matches = true;

    /*
    if (filters.status && cryptoOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  brands: IBrand[],
  page: number,
  limit: number
): IBrand[] => {
  return brands.slice(page * limit, page * limit + limit);
};






const BrandsTable: FC<BrandsTableProps> = ({ brands }) => {

  const [selectedBrands, setSelectedBrands] = useState<string[]>([]);
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);
  const [data, setData] = useState<IBrand>(null);
  const [modal, setModal] = useState<Boolean>(false)


  const selectedBulkActions = selectedBrands.length > 0;
  const exportFile = useCallback(() => {
    const ws = utils.json_to_sheet(brands);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, "Data");
    writeFileXLSX(wb, "Marcas.xlsx");
  }, [brands]);
  const handleSelectAllBrands = (
    event: ChangeEvent<HTMLInputElement>
  ): void => {
    setSelectedBrands(
      event.target.checked
        ? brands.map((brand) => brand.id)
        : []
    );
  };


  const handleSelectOneBrand = (event: ChangeEvent<HTMLInputElement>, brand: string): void => {
    if (!selectedBrands.includes(brand)) {
      setSelectedBrands((prevSelected) => [
        ...prevSelected,
        brand
      ]);
    } else {
      setSelectedBrands((prevSelected) =>
        prevSelected.filter((id) => id !== brand)
      );
    }
  };


  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };


  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredBrands = applyFilters(brands);

  const paginatedBrands = applyPagination(
    filteredBrands,
    page,
    limit
  );

  const selectedSomeBrands =
    selectedBrands.length > 0 &&
    selectedBrands.length < brands.length;
  const selectedAllBrands =
  selectedBrands.length === brands.length;
  const theme = useTheme();

  async function queryBrandById(idBrand: any){
    setModal(!modal)
    setData(null)
    const {data} = await api.get(`/marcas/${idBrand}`);
    if(data){
      setData(data);
    }else{
      setData(null);
    }
  }

  

  return (
    <Card>
      {selectedBulkActions && (
        <Box flex={1} p={2}>
          <BulkActions id_brand={selectedBrands}/>
        </Box>
      )}
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>
                <FaCloudDownloadAlt onClick={exportFile} style={{
                    textAlign: 'right',
                    position: 'relative',
                    left: '7rem',
                    height: '25px',
                    width: '25px',
                    cursor: 'pointer'
                }}/>
            </Box>
          }
          title="Marcas cadastradas"
        />
      )}
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                
              </TableCell>
              <TableCell>Nome marca</TableCell>
              <TableCell>ID Marca</TableCell>
              <TableCell align="right">Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedBrands.map((brands) => {
              const isBrandSelected = selectedBrands.includes(
                brands.id
              );
              return (
                <TableRow
                  hover
                  key={brands.id}
                  selected={isBrandSelected}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isBrandSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneBrand(event, brands.id)
                      }
                      value={isBrandSelected}
                    />
                  </TableCell>

                  <TableCell>
                    <Typography variant="body2" color="text.secondary" noWrap>
                      {(brands.nome)}
                    </Typography>
                  </TableCell>

                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {brands.identificador}
                    </Typography>
                  </TableCell>
                 
                  <TableCell align="right">
                    <Tooltip title="Editar Marca" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryBrandById(brands.id)}
                      >
                        <EditTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                  </TableCell>

                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredBrands.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
       {data && modal && (<BrandsModal 
        openModal={modal}
        setModal={setModal}
        data={data}
        edit={true}
      />)}
    </Card>
  );
};

BrandsTable.propTypes = {
  brands: PropTypes.array.isRequired
};

BrandsTable.defaultProps = {
  brands: []
};

export default BrandsTable;
